import React from 'react';
import { DistrictsMap } from '../Districts/DistrictsMap';


export const FarEast = () => {
    const farEast = "Дальневосточный федеральный округ";
    const subjectId = 27;

    return (
        <>
            <DistrictsMap 
                subject={farEast} 
                subjectId={subjectId}
            />
        </>
    )
}