import React from 'react';
import { DistrictsMap } from '../Districts/DistrictsMap';


export const Volga = () => {
    const volga = "Приволжский федеральный округ";
    const subjectId = 55;

    return (
        <>
            <DistrictsMap 
                subject={volga} 
                subjectId={subjectId}
            />
        </>
    )
}