import React from 'react';
import { DistrictsMap } from '../Districts/DistrictsMap';

export const Ural = () => {
    const ural = "Уральский федеральный округ";
    const subjectId = 89;

    return (
        <>
            <DistrictsMap 
                subject={ural} 
                subjectId={subjectId}
            />
        </>
    )
}