import React from 'react';
import { DistrictsMap } from '../Districts/DistrictsMap';


export const Southern = () => {
    const southern = "Южный федеральный округ";
    const subjectId = 3;

    return (
        <>
            <DistrictsMap 
                subject={southern} 
                subjectId={subjectId}
            />
        </>
    )
}