import React from 'react';
import { DistrictsMap } from '../Districts/DistrictsMap';


export const Siberia = () => {
    const siberia = "Сибирский федеральный округ"
    const subjectId = 60;
    
    return (
        <>
            <DistrictsMap 
                subject={siberia} 
                subjectId={subjectId}
            />
        </>
    )
}