import React from 'react';
import { DistrictsMap } from '../Districts/DistrictsMap';


export const NorthWestern = () => {
    const northWestern = "Северо-западный федеральный округ";
    const subjectId = 49;

    return (
        <>
            <DistrictsMap 
                subject={northWestern} 
                subjectId={subjectId}
            />
        </>
    )
}