import React from 'react';
import { DistrictsMap } from '../Districts/DistrictsMap';


export const NorthCaucasian = () => {
    const northCaucasian = "Северо-Кавказский федеральный округ";
    const subjectId = 8;

    return (
        <>
            <DistrictsMap 
                subject={northCaucasian}
                subjectId={subjectId}
            />
        </>
    )
}