import React from 'react';
import { DistrictsMap } from '../Districts/DistrictsMap';


export const Central = () => {
    const central = "Центральный федеральный округ";
    const subjectId = 11;

    return (
        <>
            <DistrictsMap 
                subject={central}
                subjectId={subjectId}
            />
        </>
    )
}